#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.App {
    text-align: center;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50vh;
}

.App p {
    font-size: 12px;
    width: 50%;
}

.form {
    display: flex;
    flex-direction: column;
}
.form input {
    box-sizing: border-box;
    max-width:100%;
    width: 80%;
}
.form input[type="text"] {
    height: 30px;
    margin-bottom: 10px;
}

.form input[type="submit"] {
    padding: 5px;
    height: 30px;
    border: none;
    background-color: #187dbc;
    color: #fff;
}

.form input[type="submit"]:hover {
    border: #fff;
    cursor: pointer;
    background-color: #000;
    color: #fff;
}

.error {
    color: red;
    font-size: 10px;
    text-align: center;
}

.message {
    font-size: 15px !important;
}

body {
    background-color: #f5f5f5;
    font: 600 18px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Lato, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    color: #4b4b4b;
}

.container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 50px);
    grid-gap: 3px;
    margin-top: 15px;
}

.chatWindow {

    min-width:320px;
    width:80%;
    background: rgb(233, 229, 229);
    border-radius: 5px;
}

.chatInputWrapper {
}

::-webkit-scrollbar {
    display: none;
}

/* M E S S A G E S */

.chat {
    list-style: none;
    background: none;
    margin: 0;
    padding: 0 0 50px 0;
    /* margin-top: 60px; */
    margin-bottom: 10px;
    max-height: 400px;
    overflow: scroll;
    scroll-behavior: smooth;
}

.chat li {
    padding: 0.5rem;
    overflow: hidden;
    display: flex;
}

.other .msg {
    min-width: 50px;
    order: 1;
    border-top-left-radius: 0px;
    box-shadow: -1px 2px 0px #d4d4d4;
    overflow-wrap: break-word;
}

.other.whiteBackground:before {
    content: "";
    position: relative;
    top: 0px;
    right: 0px;
    left: 40px;
    width: 0px;
    height: 0px;
    border: 5px solid #fff;
    border-left-color: transparent;
    border-bottom-color: transparent;
}
.other.grayBackground:before {
    content: "";
    position: relative;
    top: 0px;
    right: 0px;
    left: 40px;
    width: 0px;
    height: 0px;
    border: 5px solid lightgray;
    border-left-color: transparent;
    border-bottom-color: transparent;
}
.self {
    justify-content: flex-end;
    align-items: flex-end;
}

.self .msg {
    order: 1;
    border-bottom-right-radius: 0px;
    box-shadow: 1px 2px 0px #d4d4d4;
}
.whiteBackground {
    background: white;
}
.grayBackground {
    background:lightgray;
}
.msg {
    min-width: fit-content;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.07);
}

.msg p {
    font-size: 0.8rem;
    margin: 0 0 0.2rem 0;
    color: rgb(81, 84, 255);
}

.msg time {
    font-size: 0.7rem;
    color: #ccc;
    margin-top: 3px;
    float: right;
    cursor: default;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.msg time:before {
    content: " ";
    color: #ddd;
    font-family: FontAwesome;
    display: inline-block;
    margin-right: 4px;
}


::-webkit-scrollbar {
    min-width: 12px;
    width: 12px;
    max-width: 12px;
    min-height: 12px;
    height: 12px;
    max-height: 12px;
    background: #e5e5e5;
}

::-webkit-scrollbar-thumb {
    background: rgb(48, 87, 158);
    border: none;
    border-radius: 100px;
    border: solid 3px #e5e5e5;
    box-shadow: inset 0px 0px 3px #999;
}

::-webkit-scrollbar-thumb:hover {
    background: #b0b0b0;
    box-shadow: inset 0px 0px 3px #888;
}

::-webkit-scrollbar-thumb:active {
    background: #aaa;
    box-shadow: inset 0px 0px 3px #7f7f7f;
}

::-webkit-scrollbar-button {
    display: block;
    height: 26px;
}

/* T Y P E */

input.textarea {
    background: #fafafa;
    border: none;
    outline: none;
    padding-left: 55px;
    padding-right: 55px;
    color: #666;
    font-weight: 400;
}
.form {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.submitButton{
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    border-radius: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.offline {
    width: max-content;
    background-color: lightgray;
    padding: 10px;
    border-radius: 20px;
}
.online {
    width: max-content;
    background-color: lightgreen;
    padding: 10px;
    border-radius: 20px;
}
.statusWrapper {
    display: flex;
    justify-content: center;
}